import React, { useEffect } from 'react';
import { useStore } from '../hooks/useStore';
import { turnOffCam } from './AppManager'

const appStates= {
  SPLASH: 'splash',
  DIRECTIONS: 'directions',
  COUNTDOWN: 'countdown',
  PLAYING: 'playing',
  END: 'end',
  PHOTOTAKEN: 'phototaken'
}



export const GameManager = (props) => {
  const [setTimeLeft, setAppState, setGaugeRunning, setEndVisibility, timerRunning, setTimerRunning] = useStore((state) => [
    state.setTimeLeft,
    state.setAppState,
    state.setGaugeRunning,
    state.setEndVisibility,
    state.timerRunning,
    state.setTimerRunning,
  ]);

  useEffect(() => {
    let timer;
    let internalTimeLeft = 24999;
    const startTime = new Date();
    if(timerRunning){
      timer = setInterval(() => {
        let current = new Date();
        let count = current - startTime;
        internalTimeLeft = 24999-count;
        setTimeLeft(internalTimeLeft);
        if(internalTimeLeft <= 199){
          setAppState(appStates.END);
          setGaugeRunning(false);
          setTimeLeft(0);
          turnOffCam();
          setTimeout(function() { 
            setEndVisibility(true);
          }, 2000);
          setTimerRunning(false);
        }
      }, 100);
    }
    return() => {clearInterval(timer)}
  },[timerRunning])

  return null;
}