import React, { useState, useEffect } from 'react';
import { useStore } from '../hooks/useStore';
import gauge from '../images/UI_Metter_Container.png'
import top from '../images/UI_Score.png'
import shootBar from '../images/UI_Meter_Range_Unlit_v2.png'
import shootBarLit from '../images/UI_Meter_Range_Lit_v2.png'
import gaugeBottom from '../images/UI_Meter_Crescent.png'
import lightningRing1 from '../images/UI_Meter_bolt01.png'
import lightningRing2 from '../images/UI_Meter_bolt02.png'

const gaugePositions= {
  LEFT: 'left',
  CENTER: 'center',
  RIGHT: 'right'
}

export const Gauge2 = (props) => {
  const [timeLeft, points, ctdown, setGaugePosition, gaugePosition, rapidFire, gaugeRunning, scorePActive, rFirePActive, blockingLeft, blockingRight] = useStore((state) => [
    state.timeLeft,
    state.points,
    state.ctdown,
    state.setGaugePosition,
    state.gaugePosition,
    state.rapidFire,
    state.gaugeRunning,
    state.scorePActive,
    state.rFirePActive,
    state.blockingLeft,
    state.blockingRight,
  ]);

  useEffect(() => {
    let id1,id2, id3, id4;
    
    if(gaugeRunning){
      setGaugePosition(gaugePositions.LEFT);
      setTimeout(function() { 
        setGaugePosition(gaugePositions.CENTER);
        id1 = setInterval(() => {
          setGaugePosition(gaugePositions.CENTER);
        }, 2000);
      }, 400);
      setTimeout(function() { 
        setGaugePosition(gaugePositions.RIGHT);
        id2 = setInterval(() => {
          setGaugePosition(gaugePositions.RIGHT);
        }, 2000);
      }, 600);
      setTimeout(function() { 
        setGaugePosition(gaugePositions.CENTER);
        id3 = setInterval(() => {
          setGaugePosition(gaugePositions.CENTER);
        }, 2000);
      }, 1400);
      setTimeout(function() { 
        setGaugePosition(gaugePositions.LEFT);
        id4 = setInterval(() => {
          setGaugePosition(gaugePositions.LEFT);
        }, 2000);
      }, 1600);
    }
    return () => {
      clearInterval(id1);
      clearInterval(id2);
      clearInterval(id3);
      clearInterval(id4);
    };
  }, [gaugeRunning]);
  
  const [markerColor, setMarkerColor] = useState('white');
  const [markerGlow, setMarkerGlow] = useState('0 0 .5vw .125vw #fff, 0 0 2vw .75vw #0ff');
  useEffect(() =>{
    if(blockingLeft||blockingRight){
      setMarkerColor('red');
      setMarkerGlow('0 0 2vw 1.5vw red, 0 0 8vw 2.5vw red')
      setTimeout(function() { 
        setMarkerColor('white');
        setMarkerGlow('0 0 .5vw .125vw #fff, 0 0 2vw .75vw #0ff')
      }, 200);
    }
  },[blockingRight,blockingLeft]);

  const [lightringO, setLightringO] = useState(0);
  const [lightringO2, setLightringO2] = useState(0);
  useEffect(() =>{
    if(scorePActive){
      setLightringO(1);
      setTimeout(function() { 
        setLightringO(0);
      }, 200);
    };
  },[scorePActive]);
  useEffect(() =>{
    if(rFirePActive){
      setLightringO2(1);
      setTimeout(function() { 
        setLightringO2(0);
      }, 3000);
    };
  },[rFirePActive]);

  return(
    <div>
      <div className="score-time" style={{ backgroundImage: `url(${top})`, backgroundSize: '100% 100%', fontFamily:'Elsner', position: 'absolute', width: '100%', height: '50vw', top: 0, zIndex: 1, transform: 'translate(0%, -57%)'}} >
        <div style={{  color: '#004f91', fontSize: '5vw',  position: 'absolute', bottom: '19%', left: '24%'}} >00:{Math.floor((timeLeft /  10000)%10)}{Math.floor((timeLeft /  1000)%10)}</div>
        <div style={{  color: '#43cbe8', fontSize: '4vw',  position: 'absolute', bottom: '23%', left: '65%'}} >{(points<10)? '0'+points : points}</div>
      </div>
      <div className='centered' style={{ fontFamily: 'Elsner', textAlign: 'center', color: 'white', fontSize: '40vw', zIndex: 9}}>
        {ctdown}
      </div>
      <div style={{position: 'absolute', bottom: '4.5%', left: '0', width: '100%', transform: 'scale(.65)', opacity: lightringO||lightringO2? 1:0, zIndex: "5",}}>
        <img className="cycle1" style={{position: 'absolute', width: '108%', left: '-4%', bottom: 0, zIndex: 1, transform: 'translate(0%, 20%)'}} src={lightningRing1} alt="splash screen" />
        <img className="cycle2" style={{position: 'absolute', width: '108%', left: '-4%', bottom: 0, zIndex: 1, transform: 'translate(0%, 20%)'}} src={lightningRing2} alt="splash screen" />
      </div>
      <div style={{position: 'absolute', bottom: '4.5%', left: '0', width: '100%', transform: 'scale(.65)', zIndex: '1'}}>
        <img style={{position: 'absolute', width: '108%', left: '-4%', bottom: 0, zIndex: 1, transform: 'translate(0%, 20%)'}} src={gauge} alt="splash screen" />
        {!rapidFire && <div>
            <img style={{position: 'absolute', width: '108%', left: '-4%', bottom: 0, zIndex: 1, transform: 'translate(0%, 20%)'}} src={shootBar} alt="splash screen" />
            <img style={{position: 'absolute', width: '108%', left: '-4%', bottom: 0, zIndex: 1, transform: 'translate(0%, 20%)', visibility: gaugePosition===gaugePositions.CENTER? 'visible' : 'hidden'}} src={shootBarLit} alt="splash screen" />
        </div>}
        {gaugeRunning && <div className="wrapper" >
          <div className="dial-container container1" >
            <div className="wedge" ></div>
          </div>
          <div className="dial-container container2" >
            <div className="wedge" ></div>
          </div>
          <div className="marker end" style={{background: markerColor, boxShadow: markerGlow}}></div>
        </div>}
        {rapidFire && <div className="wrapper2" >
          <div className="dial-container2 container1B" >
            <div className="wedge2" ></div>
          </div>
          <div className="dial-container2 container2B" >
            <div className="wedge2" ></div>
          </div>
        </div>}
        <img style={{position: 'absolute', width: '108%', left: '-4%', bottom: 0, zIndex: 1, transform: 'translate(0%, 20%)'}} src={gaugeBottom} alt="splash screen" />
      </div>
    </div>
  )
}                                     