import React, { useState, useRef, useFrame } from 'react';
import * as THREE from 'three'
import { useStore } from '../hooks/useStore';
import {useSpring, animated} from 'react-spring'
import useSound from 'use-sound';

import bgImg from '../images/splash_background_v3.jpg'
import endScreen from '../images/UI_Results_BG_v4.png'
import thankyou from '../images/UI_Thanks.png'
import instruct1 from '../images/UI_Instruction1.png'
import instruct2 from '../images/UI_Instruction2.png'
import playAgain from '../images/UI_PlayAgain_Active.png'
import takeSelfie from '../images/UI_TakeSelfie_Active.png'
import shareInactive from '../images/UI_Share_Inactive.png'
import loadingBall from '../images/UI_Basketball_Icon.png'
import loadingSpin from '../images/UI_Loading_Icon.png'
import loadingLogo from '../images/UI_LoadingScreen_Logo.png'
import camButton from '../images/UI_Camera_Inactive.png'
import logo from '../images/UI_Masketball_Logo.png'
import fgTop from '../images/splash_DarkBlue_v1.png'
import fgBottom from '../images/splash_LightBlue_v1.png'
import fgBottomWhite from '../images/splash_white_v1.png'
import selfieTop from '../images/UI_Selfie_TopBG.png'
import selfieBottom from '../images/UI_Selfie_BottomBG.png'
import volOn from '../images/UI_Volume_Active.png'
import volOff from '../images/UI_Volume_Inactive.png'
import vaccineActive from '../images/UI_Vaccine_Active.png'
import scheduleInactive from '../images/UI_Schedule_Inactive.png'
import activitiesInactive from '../images/UI_Activities_Inactive.png'
import photoTakenFooter from '../images/UI_FinalScreen_Footer_BG.png'
import retakeSelfieButton from '../images/UI_RetakeSelfie_Active_v2.png'
import playAgainSelfie from '../images/UI_PlayAgain_EndScreen_Active.png'


import launchSound from '../sounds/Launch_Button_1.mp3'
import gMusic from '../sounds/Game_Music_Loop_2.mp3'
import mMusic from '../sounds/Menu_Music_Loop_1.mp3'

import {takePhoto, share, clearState, renderPrediction} from '../facetrack'

import {LaunchButton} from './UI_Elements/LaunchButton'
import {MainLogo} from './UI_Elements/MainLogo'
import {IntroMavsman} from './UI_Elements/IntroMavsman'
import {IntroChamp} from './UI_Elements/IntroChamp'
import {ReadyButton} from './UI_Elements/ReadyButton'


const appStates= {
  SPLASH: 'splash',
  DIRECTIONS: 'directions',
  COUNTDOWN: 'countdown',
  PLAYING: 'playing',
  END: 'end',
  PHOTOTAKEN: 'phototaken'
}

export let cameraOn;
export function turnOffCam(){
  cameraOn = false;
}
cameraOn = false;

export const AppManager = (props) => {
  const [
    setAppState, 
    appState, 
    setTimerRunning, 
    resetTime, 
    resetPoints, 
    setGaugeRunning, 
    setCtdown, 
    endVisibility, 
    setEndVisibility, 
    points, 
    setLaunchButtonOn,
    setMainLogoOn,
    setMavsOn,
    setChampOn,
    setReadyButtonOn,
    volume,
    setVolume,
  ] = useStore((state) => [
      state.setAppState,
      state.appState,
      state.setTimerRunning,
      state.resetTime,
      state.resetPoints,
      state.setGaugeRunning,
      state.setCtdown,
      state.endVisibility,
      state.setEndVisibility,
      state.points,
      state.setLaunchButtonOn,
      state.setMainLogoOn,
      state.setMavsOn,
      state.setChampOn,
      state.setReadyButtonOn,
      state.volume,
      state.setVolume,
  ]);

  // const sound = new Audio(launchSound);
  // // const gameMusic = new Audio(gMusic);
  // const menuMusic = new Audio(mMusic);


  const [playLaunch, launchExp] = useSound(launchSound, {html5:true, volume});
  const [playGameMusic, gameMusicExp] = useSound(gMusic, {html5:true, volume});
  const [playMenuMusic, menuMusicExp] = useSound(mMusic, {html5:true, volume});

  // React.useEffect(() => {
  //   window.addEventListener('touchstart', () => {
  //     sound.muted = false
  //     sound.play()
  //   })
  // })

  
  const [selfieMode, setSelfieMode] = useState(false)
  const [photoTaken, setPhotoTaken] = useState(false)
  const [launchScreenVis, setLaunchScreenVis] = useState(true)
  const [fgBottomScrolling, setFgBottomScrolling] = useState(false)

  const [inst1o, setInst1o] = useState(0)
  const [inst2o, setInst2o] = useState(0)
  // const [introO, setIntroO] = useState(1)
  const [bScrollZind, setBScrollZind] = useState(1)
  const [tScrollZind, setTScrollZind] = useState(11)

  const [loaded, setLoaded] = useState(false)
  const handleTrackingReady = (event) => {
    setLoaded(true);
  };
  React.useEffect(() => {
    document.addEventListener('trackingReady', handleTrackingReady);
    return () => {
      document.addEventListener('trackingReady', handleTrackingReady);
    };
  }, []);

  // const [instructScreenVis, setInstructScreenVis] = useState(true)
  // const [endScreenVis, setEndScreenVis] = useState("hidden")
  // const visref = useRef("visible");
  // const togvis = () => {
  //   console.log("click")
  //   if(test === "hidden"){setTest("visible")}
  //   else {setTest("hidden")}
  // }


  React.useEffect(() => {
    if(endVisibility){
      playMenuMusic();
      gameMusicExp.stop();
      setBScrollZind(11);
    }
  },[endVisibility])

  const isMounted = useRef(false);
  React.useEffect(() => {
    if (isMounted.current) {
      startIntro();
    } else {
      isMounted.current = true;
    }
  }, [loaded]);

  const startIntro = () => {
    setMavsOn(0);
    setChampOn(0);
    setLaunchButtonOn(0);
    setMainLogoOn(0);
    setTimeout(function() { 
      setMainLogoOn(1);
      setTimeout(function() { 
        setMavsOn(1);
        setTimeout(function() { 
          setChampOn(1);
          setTimeout(function() { 
            setLaunchButtonOn(1);
          }, 1000);
        }, 300);
      }, 300);
    }, 1000);
  }
  const handleLaunchPressed = () => {
    playLaunch();
    playMenuMusic();
    setTimeout(function() { 
      setAppState(appStates.DIRECTIONS);
      setMavsOn(0);
      setChampOn(0);
      setLaunchButtonOn(0);
      setTimeout(function() { 
        setInst1o(1);
        setTimeout(function() { 
          setInst2o(1);
          setTimeout(function() { 
            setReadyButtonOn(1);
          }, 2000);
        }, 2000);
      }, 1000);
    }, 300);
  }
  const handleReadyPressed = () => {
    playLaunch();
    menuMusicExp.stop();
    playGameMusic();
    startCountdown();
  }
  const startCountdown = () =>{
    setAppState(appStates.COUNTDOWN);
    setAppState(appStates.COUNTDOWN);
    setLaunchScreenVis(false);
    setFgBottomScrolling(true);
    cameraOn = true;
    renderPrediction();
    document.getElementById('webcam').style.zIndex = -1;
    setEndVisibility(false);
    setBScrollZind(1);
    resetTime();
    resetPoints();
    setTimeout(function() { 
      setCtdown('3');
      setTimeout(function() { 
        setCtdown('2');
        setTimeout(function() { 
          setCtdown('1');
          setTimeout(function() { 
            setCtdown('');
            startGame();
          }, 1000);
        }, 1000);
      }, 1000);
    }, 200);
  }
  const startGame = () =>{
    setAppState(appStates.PLAYING);
    setTimerRunning(true);
    setGaugeRunning(true);
  }
  const clearPhoto = () =>{
    setPhotoTaken(false);
    clearState();
  }
  return(
    <div>
      {volume===1
        ? <img src={volOn} onClick={() => {setVolume(0)}} style={{position: 'absolute', width: '8vw', top: '4vw', right: '4vw', zIndex: '20'}} alt="vol" />
        : <img src={volOff} onClick={() => {setVolume(1)}} style={{position: 'absolute', width: '8vw', top: '4vw', right: '4vw', zIndex: '20'}} alt="vol" />
      }

      {!loaded && <div className="loadingscreen" style={{backgroundImage: 'linear-gradient(to bottom right, black, #004f91)', position: 'absolute', width: '100%', height: '100%', zIndex: 99999,}} >
        <img className="centered" src={loadingLogo} style={{width: '70vw', top: '80px', zIndex: '14'}} alt="splash screen" />
        <img className="centered" src={loadingBall} style={{width: '30%'}} alt="splash screen" />
        <img className="rotateme" src={loadingSpin}  alt="splash screen" />
        <div style={{fontFamily: 'FugazOne-Regular', textAlign: 'center', color: 'white', fontSize: '20px', position: 'absolute', bottom: '35%', left: '50%', transform: 'translate(-50%, 0)'}}>
          LOADING...
        </div>
      </div>}

      {launchScreenVis && <div className="launchscreen containeralt" style={{ zIndex: '12' }}>

        {/* <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: '11', transform: 'translate(-50%,-50%) rotate(-10.5deg)'}}>
          <div style={{background: `url(${bgImg})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', top: '-10vh', left: '-30vh', width: '360vh', height: '120vh', zIndex: '12',}} />
        </div> */}
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: '12', transform: 'translate(-50%,-50%) rotate(-10.5deg)'}}>
          <div className="sliding-background" style={{backgroundImage: `url(${bgImg})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', top: '-30vh', left: '-50vh', width: '640vh', height: '160vh', zIndex: '12',}} />
        </div>
        
        <IntroMavsman />
        <IntroChamp />
        <MainLogo />
        <LaunchButton launchPressed={handleLaunchPressed} />
        <div style={{fontFamily: 'FugazOne-Regular', textAlign: 'center', color: 'white', fontSize: '10px', position: 'absolute', zIndex: '15', bottom: '10px', left: '50%', width: '100%', transform: 'translate(-50%, 0)'}}>
          Copyright © 2021 NBA Media Ventures, LLC. All rights reserved.
        </div>
        <div style={{position: 'absolute', top: '52%', left: '0', zIndex: '13',}} >
          <img id="inst1" style={{position: 'absolute', left: '0vw', width: '85vw', transform: 'translate(7.5vw, -18vh)', opacity: inst1o}} src={instruct1} alt="thank you" />
          <img id="inst2" style={{position: 'absolute', left: '0vw', width: '85vw', transform: 'translate(7.5vw, -3vh)', opacity: inst2o}} src={instruct2} alt="thank you" /> {/*left -2, 4. rotate 15 deg width 65vw*/}
        </div>
        <ReadyButton readyPressed={handleReadyPressed} />
      </div>}

      {endVisibility && <div className="endscreen" >
        <div style={{backgroundImage: 'linear-gradient(to bottom right, white, #004f91)', position: 'absolute', width: '100%', height: '100%', zIndex: '9'}}/>
        <img className="centered" src={endScreen} alt="splash screen" />

        {/* <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: tScrollZind, transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div style={{background: `url(${fgTop})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', top: '-25vh', left: '-30vh', width: '360vh', height: '60vh', zIndex: '11',}} />
        </div> */}
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: tScrollZind, transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div className="sliding-foreground" style={{background: `url(${fgTop})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', top: '-25vh', left: '-30vh', width: '480vh', height: '60vh', zIndex: '12',}} />
        </div>
        
        <div style={{fontFamily: 'Elsner', fontSize: '30px', color: 'orange', position: 'absolute', top: '10px', left: '18px',  zIndex: '11'}}>
          <div >
            SCORE
          </div>
        </div>
        <div style={{fontFamily: 'Elsner', fontSize: '80px', color: 'white', position: 'absolute', top: '-3px', left: '110px',  zIndex: '11'}}>
          <div >
            {(points<10)? '0'+points : points}
          </div>
        </div>
        <img style={{position: 'absolute', top: '15vh', right: '15px', width: '55vw', zIndex: '12'}} src={thankyou} alt="thank you" />
        <img style={{position: 'absolute', bottom: '67px', right: '199px', height: '60px', zIndex: '12', transform: 'translate(0, -30px)'}} src={playAgain} onClick={()=>{handleReadyPressed() }} alt="thank you" />
        <img style={{position: 'absolute', bottom: '96px', right: '16px', height: '60px', zIndex: '12', transform: 'translate(0, -30px)'}} src={takeSelfie} onClick={()=>{document.getElementById('webcam').style.zIndex = 12; setSelfieMode(true); setTScrollZind(13)}} alt="thank you" />
        <img style={{position: 'absolute', bottom: '31px', right: '87px', height: '60px', zIndex: '12', transform: 'translate(0, -30px)'}} src={vaccineActive} onClick={()=>{window.location.href = "https://www.utswmed.org/pediatrics/";}} alt="thank you" />
        <img style={{position: 'absolute', bottom: '-20px', right: '186px', height: '50px', zIndex: '12', transform: 'translate(0, -30px)'}} src={scheduleInactive} onClick={()=>{window.location.href = "https://www.mavs.com/schedule/";}} alt="thank you" />
        <img style={{position: 'absolute', bottom: '2px', right: '38px', height: '50px', zIndex: '12', transform: 'translate(0, -30px)'}} src={activitiesInactive} onClick={()=>{window.location.href = "https://www.mavs.com/mascots/";}} alt="thank you" />


      </div> }

      {fgBottomScrolling && <div>
        {/* <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: bScrollZind, transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div style={{background: `url(${fgBottom})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', bottom: '-60vh', left: '-30vh', width: '360vh', height: '60vh', zIndex: '11',}} />
        </div> */}
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: bScrollZind, transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div className="sliding-foreground" style={{background: `url(${fgBottom})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', bottom: '-50vh', left: '-30vh', width: '480vh', height: '60vh', zIndex: '12',}} />
        </div>
      </div>}

      {selfieMode && <div className="selfie-mode" >
        <div style={{backgroundImage: 'linear-gradient(to bottom right, #004f91, white)', position: 'absolute', width: '100%', height: '100%', zIndex: '11'}}/>
        {/* <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: '13', transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div style={{background: `url(${fgBottomWhite})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', bottom: '-65vh', left: '-30vh', width: '360vh', height: '60vh', zIndex: '11',}} />
        </div> */}
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: '13', transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div className="sliding-foreground" style={{background: `url(${fgBottomWhite})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', bottom: '-50vh', left: '-30vh', width: '480vh', height: '60vh', zIndex: '12',}} />
        </div>
        <div style={{fontFamily: 'Elsner', fontSize: '30px', color: 'orange', position: 'absolute', top: '10px', left: '18px',  zIndex: '13'}}>
          <div >
            SCORE
          </div>
        </div>
        <div style={{fontFamily: 'Elsner', fontSize: '80px', color: 'white', position: 'absolute', top: '-3px', left: '110px',  zIndex: '13'}}>
          <div >
            {(points<10)? '0'+points : points}
          </div>
        </div>
        <img id="logo" src={logo} style={{position: 'absolute', width: '80vw', bottom: '20vw', left: '50%', transform: 'translate(-50%, 0)', zIndex: '14'}} alt="splash screen" />
        <img id="selfietop" src={selfieTop} style={{display: 'none'}} alt="splash screen" />
        <img id="selfiebottom" src={selfieBottom} style={{display: 'none'}} alt="splash screen" />
        <img src={camButton} style={{position: 'absolute', width: '15vw', bottom: '3vw', left: '50%', transform: 'translate(-50%, 0)', zIndex: '14'}} alt="splash screen" onClick={()=>{takePhoto((points<10)? '0'+points : points.toString()), setPhotoTaken(true)}} />
      </div>}

      {photoTaken && <div className="photo-taken" style={{backgroundImage: 'linear-gradient(to bottom right, black, #004f91)', position: 'absolute', width: '100%', height: '100%', zIndex: '15',}} >
        <img id="image-preview" style={{position: 'absolute', zIndex: '16', width: '90vw', top: '0vw', left: '5vw'}}></img>
        <img src={photoTakenFooter} style={{position: 'absolute', width: '100%', bottom: '0', left: '0', transform: 'translate(0%, 0)', zIndex: '17'}} alt="thank you" />
        <img src={shareInactive} style={{position: 'absolute', width: '36vw', bottom: '14.7vw', left: '50%', transform: 'translate(19%, 0)', zIndex: '17'}} onClick={()=>{ share(); }} alt="thank you" />
        <img src={playAgainSelfie} style={{position: 'absolute', width: '45vw', bottom: '8vw', left: '50%', transform: 'translate(-99%, 0)', zIndex: '17'}} onClick={()=>{setSelfieMode(false); clearPhoto(); setTScrollZind(11); handleReadyPressed() }} alt="thank you" />
        <img src={retakeSelfieButton} style={{position: 'absolute', width: '54vw', bottom: '28vw', left: '50%', transform: 'translate(-50%, 0)', zIndex: '17'}} onClick={()=>{clearPhoto();}} alt="thank you" />
      </div>}

    </div> 
  )
}